import { MouseEvent, useRef, useState } from "react";
import { Link, useNavigate } from 'react-router-dom';
import arvind_img from '../../../assets/img/doctor/drarvind.jpg';
import namrata_img from '../../../assets/img/doctor/drnamrata.jpg';
import FullscreenSearch from '../../../compoments/common/FullscreenSearch';
import { APP_ROUTES, SH_DOCTOR } from '../../../utils/constant';
import MenuData from "./MenuData";


const NavMenuFive = ({ videoHeader, home_4 }: any) => {
  const navigate = useNavigate();
  const [openSearch, setOpenSearch] = useState<boolean>(false);
  // const [submenuDr01Show, setSubmenuDr01Show] = useState<string>("");
  // const [submenuDr02Show, setSubmenuDr02Show] = useState<string>("");
  const ref1 = useRef<HTMLDivElement | null>(null);
  const ref2 = useRef<HTMLDivElement | null>(null);
  const drLinkMouseOverHandler = (index: number) => {
    ref1.current?.classList.remove("submenu-dr-01")
    if (index === 0) {
      ref1.current?.classList.add("submenu-dr-01-show")
      ref2.current?.classList.remove("submenu-dr-02-show")
    } else if (index === 1) {
      ref1.current?.classList.remove("submenu-dr-01-show")
      ref2.current?.classList.add("submenu-dr-02-show")
    }
  }

  const submenuMouseLeaveHandler = () => {
    ref1.current?.classList.remove("submenu-dr-01-show")
    ref2.current?.classList.remove("submenu-dr-02-show")
    ref1.current?.classList.add("submenu-dr-01")
  }

  const drLinkClickHandler = (e: MouseEvent<HTMLAnchorElement>) => {
    e.preventDefault();
  }

  const knowMoreBtnClick = (type: string) => {
    // ref1.current?.classList.add("submenu-dr-01")
    if (type === APP_ROUTES.DR_NAMRATA) {
      navigate(APP_ROUTES.DR_NAMRATA)
    } else if (type === APP_ROUTES.DR_ARVIND) {
      navigate(APP_ROUTES.DR_ARVIND)

    }
  }

  return (
    <>
      <ul>
        {MenuData.map((item, i) => (
          <li key={i}>
            {item.has_dropdown ?
              <Link to={item.link} className={`${videoHeader ? "header__menu-3 " : ""}`}>{item.title}</Link>
              : <Link to={item.link} className={`${videoHeader ? "header__menu-3 " : ""}`}>{item.title.replace("+", "")}</Link>
            }
            {item.has_dropdown &&
              <ul className="submenu" onMouseLeave={submenuMouseLeaveHandler}>
                <div className="container">
                  <div className="row">
                    {item.title !== 'Doctors +' && item.menus?.map((menu, index) =>
                      <div className="col" key={index}>
                        {menu.heading &&
                          <>
                            <h6 className="text-center heading">{menu.heading}</h6>
                            <ul className="text-center">
                              {menu.sub_menus?.map((submenu, index) =>
                                <li key={index} className="border-bottom submenu__link">
                                  <Link to={submenu.link}>{submenu.title}</Link>
                                </li>
                              )}
                            </ul>
                          </>
                        }
                      </div>
                    )}
                    {item.title === 'Doctors +' &&
                      <>
                        <div className="col" >
                          <ul>
                            {item.menus?.map((menu, index) =>
                              <li key={index} onMouseOver={() => drLinkMouseOverHandler(index)} className="border-bottom submenu__link ">
                                <Link onClick={(e) => drLinkClickHandler(e)} to="#">{menu.heading}</Link>
                              </li>)}
                          </ul>
                        </div>
                        <div className="col-9 submenu-dr-col">
                          <div ref={ref1} className="submenu-dr submenu-dr-01 ">
                            <div className="row" >
                              <div className="col-3">
                                <img className="img-fluid rounded rounded-circle mb-4 border border-5" src={namrata_img} alt="Dr. Namrata" />
                              </div>
                              <div className="col-9">
                                <h5>BHMS, DRCH</h5>
                                <h6>Chief Homoeopathy Medical Officer - State Homoeopatic Dispensary, District Hospital, Mau</h6>
                                <div>
                                  Dr. {SH_DOCTOR.DR_NAMRATA} is a dedicated and compassionate BHMS-qualified doctor with 21+ years experience in homeopathic healthcare. Her journey in the field of homeopathy is marked by a commitment to providing natural and personalized healing solutions to her patients.
                                </div>
                                <div className="text-end">
                                  <button onClick={() => knowMoreBtnClick(APP_ROUTES.DR_NAMRATA)} className="primary_btn submenu-dr-btn  mt-20">Know More</button>
                                </div>
                              </div>

                            </div>
                          </div>
                          <div ref={ref2} className="submenu-dr submenu-dr-02 ">
                            <div className="row ">
                              <div className="col-3">
                                <img className="img-fluid rounded rounded-circle mb-4 border border-5" src={arvind_img} alt="Dr. Namrata" />
                              </div>
                              <div className="col-9">
                                <h5>BHMS</h5>
                                <h6>Chief Medical Officer - <br />S.H.D., Sahroj CHC Kopaganj, Mau</h6>
                                <div>
                                  Dr. {SH_DOCTOR.DR_ARVIND} is a dedicated and compassionate BHMS-qualified doctor committed to promoting holistic healing through homeopathy. Having 19+ years of experience in the field, he strives to provide personalized and natural healthcare solutions to his .
                                </div>
                                <div className="text-end">
                                  <button onClick={() => knowMoreBtnClick(APP_ROUTES.DR_ARVIND)} className="primary_btn submenu-dr-btn  mt-20">Know More</button>
                                </div>
                              </div>

                            </div>
                          </div>
                        </div>
                      </>

                    }
                  </div>
                </div>
              </ul>
            }
          </li>
        ))}

        {home_4 && <li><button style={{ marginLeft: "10px" }} className="nav-search-icon">
          <i className="fa fa-search" onClick={() => setOpenSearch(true)}></i></button>
        </li>
        }
      </ul>
      {openSearch && <FullscreenSearch openSearch={openSearch} setOpenSearch={setOpenSearch} />}
    </>
  );
};

export default NavMenuFive;

