import Breadcrumb from "../../compoments/common/Breadcrumb";
import FooterThree from "../../layout/footers/FooterThree";
import HeaderFive from '../../layout/headers/HeaderFive';
import AboutHomeopathyInnerArea from './AboutHomeopathyInnerArea';

import { useEffect } from "react";
import imgUrl from '../../assets/img/breadcrumb/about-homeopathy.jpg';
import Wrapper from "../../layout/Wrapper";
import { PAGE_TITLE } from "../../utils/constant";

const AboutHomeopathy = () => {

  useEffect(() => {
    // Set the page title when the component mounts
    document.title = PAGE_TITLE.ABOUT_HOMEOPATHIC;

    // Optionally, you can return a cleanup function to reset the title when the component unmounts
    return () => {
      document.title = PAGE_TITLE.HOME;
    };
  }, []);

  return (
    <Wrapper>
      <HeaderFive />
      <main>
        <Breadcrumb sub_title='Your Haven for Holistic Healing.' title='Discover the healing power of homeopathy' page='Homeopathy' imgUrl={imgUrl} />
        {/* <BlogArea /> */}
        <section className="blog-area pt-120 pb-80">
          <div className="container">
            <div className="row">
              <AboutHomeopathyInnerArea style_details={true} />
            </div>
          </div>
        </section>
      </main>
      <FooterThree />
    </Wrapper>
  );
};

export default AboutHomeopathy;
