import { MouseEvent, useRef, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import FullscreenSearch from "../../../compoments/common/FullscreenSearch";
import { useAuth } from "../../../context/AuthContext";
import { APP_ROUTES } from "../../../utils/constant";
import DoctorCard from '../Card/DoctorCard';
import { DoctorCardData } from '../Card/DoctorCardData';
import MenuDataSix from "./MenuData";

const NavMenuSix = ({ home_4 }: any) => {
  const { user, logout } = useAuth();
  const navigate = useNavigate();
  const [openSearch, setOpenSearch] = useState<boolean>(false);

  const ref1 = useRef<HTMLDivElement | null>(null);
  const ref2 = useRef<HTMLDivElement | null>(null);
  const drLinkMouseOverHandler = (index: number) => {
    ref1.current?.classList.remove("submenu-dr-01")
    if (index === 0) {
      ref1.current?.classList.add("submenu-dr-01-show")
      ref2.current?.classList.remove("submenu-dr-02-show")
    } else if (index === 1) {
      ref1.current?.classList.remove("submenu-dr-01-show")
      ref2.current?.classList.add("submenu-dr-02-show")
    }
  }

  const submenuMouseLeaveHandler = () => {
    ref1.current?.classList.remove("submenu-dr-01-show")
    ref2.current?.classList.remove("submenu-dr-02-show")
    ref1.current?.classList.add("submenu-dr-01")
  }

  const drLinkClickHandler = (e: MouseEvent<HTMLAnchorElement>) => {
    e.preventDefault();
  }

  const knowMoreBtnClick = (url: string) => {
    // ref1.current?.classList.add("submenu-dr-01")
    if (url === APP_ROUTES.DR_NAMRATA) {
      navigate(APP_ROUTES.DR_NAMRATA)
    } else if (url === APP_ROUTES.DR_ARVIND) {
      navigate(APP_ROUTES.DR_ARVIND)

    }
  }

  return (
    <>
      <ul>
        {MenuDataSix.map((item, i) => (
          <li key={i}>
            {item.has_dropdown ?
              <Link to={item.link}>{item.title}</Link>
              : <Link to={item.link}>{item.title.replace("+", "")}</Link>
            }
            {item.has_dropdown &&
              <ul className="submenu submenu-six" onMouseLeave={submenuMouseLeaveHandler}>
                <div className="container">
                  <div className="row">
                    {item.title !== 'Doctors +' && item.menus?.map((menu, index) =>
                      <div className="col" key={index}>
                        {menu.heading &&
                          <>
                            <h6 className="text-center">{menu.heading}</h6>
                            <ul className="text-center">
                              {menu.sub_menus?.map((submenu, index) =>
                                <li key={index} className=" submenu__link">
                                  <Link to={submenu.link}>{submenu.title}</Link>
                                </li>
                              )}
                            </ul>
                          </>
                        }
                      </div>
                    )}
                    {item.title === 'Doctors +' &&
                      <>
                        <div className="col" >
                          <ul>
                            {item.menus?.map((menu, index) =>
                              <li key={index} onMouseOver={() => drLinkMouseOverHandler(index)} className=" submenu__link submenu__link-six ">
                                <Link onClick={(e) => drLinkClickHandler(e)} to="#">{menu.heading}</Link>
                              </li>)}
                          </ul>
                        </div>
                        <div className="col-9 submenu-dr-col submenu-dr-col-six">
                          <div ref={ref1} className="submenu-dr submenu-dr-01 ">
                            <DoctorCard data={DoctorCardData.namrata} knowMoreBtnHandler={knowMoreBtnClick} />
                          </div>
                          <div ref={ref2} className="submenu-dr submenu-dr-02 ">
                            <DoctorCard data={DoctorCardData.arvind} knowMoreBtnHandler={knowMoreBtnClick} />
                          </div>
                        </div>
                      </>

                    }
                  </div>
                </div>
              </ul>
            }
          </li>
        ))}

        {/* <li >
          <Link href="/about">About</Link>
        </li>

        <li >
          <Link href="/contact">Contact</Link>
        </li> */}

        <li >
          <Link to="/">
            <i className="fa fa-user" style={{ paddingLeft: "20px", paddingRight: "20px" }}></i>
            <span style={{ marginLeft: "-15px" }}>+</span>
          </Link>
          <ul className="submenu" style={{ width: "150px" }}>
            <li >
              {user && user?.name ? <Link to="">
                <i className="fa fa-user" style={{ marginRight: "8px" }} ></i>{user?.name}</Link>
                : <Link to={APP_ROUTES.LOGIN}>
                  <i className="fa fa-sign-in" style={{ marginRight: "8px" }} ></i>Login </Link>
              }
            </li>
            <li>
              {!user && <Link to={APP_ROUTES.REGISTER}>
                <i className="fa fa-book" style={{ marginRight: "8px" }}></i>Register</Link>}
            </li>
            <li>
              {user && user?.name && <>
                <Link to={APP_ROUTES.APPOINTMENT_LIST}>
                  <i className="fa fa-fa-user" style={{ marginRight: "8px" }}></i>Your Patients</Link>
                <Link to={APP_ROUTES.HOME} onClick={() => logout()}>
                  <i className="fa fa-sign-out" style={{ marginRight: "8px" }}></i>Logout</Link>
              </>}
            </li>
          </ul>
        </li >

        {home_4 && <li><button style={{ marginLeft: "10px" }} className="nav-search-icon">
          <i className="fal fa-search" onClick={() => setOpenSearch(true)}></i></button>
        </li>
        }
      </ul >
      {openSearch && <FullscreenSearch openSearch={openSearch} setOpenSearch={setOpenSearch} />}
    </>
  );
};

export default NavMenuSix;

