
import { ToastContainer, toast } from 'react-toastify';

import { yupResolver } from '@hookform/resolvers/yup';
import { authService } from '../../services/auth.service';
import { LoginUserData } from '../../types/user';

import { useForm } from 'react-hook-form';
import { Link, useNavigate } from 'react-router-dom';
import * as yup from "yup";
import { useAuth } from '../../context/AuthContext';

const schema = yup
  .object({
    email: yup.string().required().email().label("Email"),
    password: yup.string().required().min(4).label("Password"),

  })
  .required();


const LoginArea = () => {
  const navigate = useNavigate();
  const { login } = useAuth();
  const { register, handleSubmit, reset, formState: { errors }, } = useForm<LoginUserData>({ resolver: yupResolver(schema), });

  const onSubmit = async (data: LoginUserData) => {
    await authService.login(data)
      .then((response) => {
        const userData = response.data;
        login({
          id: userData.id,
          name: userData.name,
          email: userData.email,
          mobile: userData.mobile,
          role: userData.role,
          token: userData.token
        })
        const notify = () => toast("User logged in successfully!");
        notify()
        reset();
        navigate('/');
      })
      .catch((error) => {
        console.log(error.response.data);
        const notify = () => toast(error.response.data);
        notify();
      }).finally(() => {
        // setLoading(false);
      })
    // console.log(data);
  };





  return (
    <>
      <section className="login-area pt-100 pb-100">
        <div className="container">
          <div className="row">
            <div className="col-lg-8 offset-lg-2">
              <div className="basic-login">
                <h3 className="text-center mb-60">Login From Here</h3>
                <form onSubmit={handleSubmit(onSubmit)}>
                  <label htmlFor="name">Username or email <span>**</span></label>
                  <input id="name" type="text" {...register("email")} placeholder="Enter Username or Email address..." />
                  <p className="form_error">{errors.email?.message}</p>

                  <label htmlFor="pass">Password <span>**</span></label>
                  <input id="pass" type="password" {...register("password")} placeholder="Enter password..." />
                  <p className="form_error">{errors.password?.message}</p>

                  <div className="login-action mb-20 fix">
                    <span className="log-rem f-left">
                      <input id="remember" type="checkbox" />
                      <label htmlFor="remember">Remember me!</label>
                    </span>
                    <span className="forgot-login f-right">
                      <Link to="#">Lost your password?</Link>
                    </span>
                  </div>
                  <button className="primary_btn btn-icon-green w-100">Login Now</button>
                  <div className="or-divide"><span>or</span></div>
                  <Link to="/register" className="primary_btn theme-btn-2 w-100">Register Now</Link>
                  <ToastContainer />
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default LoginArea;