
import Breadcrumb from '../../compoments/common/Breadcrumb';
import Wrapper from '../../layout/Wrapper';
import FooterThree from '../../layout/footers/FooterThree';
import HeaderFive from '../../layout/headers/HeaderFive';
import HairLossInnerArea from './HairLossInnerArea';





const HairLossPage = () => {
  return (
    <Wrapper>
      <HeaderFive />
      <main>
        <Breadcrumb sub_title='Your Haven for Holistic Healing.' title='Discover the healing power of homeopathy' page='Hair Loss' imgUrl={'/breadcrumb/about-homeopathy.jpg'} />
        {/* <BlogArea /> */}
        <section className="blog-area pt-120 pb-80">
          <div className="container">
            <div className="row">
              <HairLossInnerArea style_details={true} />
            </div>
          </div>
        </section>
      </main>
      <FooterThree />
    </Wrapper>
  );
};

export default HairLossPage;
