import { Link, useNavigate } from "react-router-dom";
import { APP_ROUTES, SH_DOCTOR } from "../../../utils/constant";

import MenuData from "./MenuData";


const MobileMenus = ({ isOpenMenu, setIsOpenMenu }: any) => {
  const navigate = useNavigate();

  const knowMoreBtnClick = (type: string) => {
    // ref1.current?.classList.add("submenu-dr-01")
    if (type === APP_ROUTES.DR_NAMRATA) {
      navigate(APP_ROUTES.DR_NAMRATA)
    } else if (type === APP_ROUTES.DR_ARVIND) {
      navigate(APP_ROUTES.DR_ARVIND)

    }
  }

  return (
    <>
      <div className={`fade offcanvas-backdrop ${isOpenMenu ? "show" : ""}`} onClick={() => setIsOpenMenu(false)}></div>
      <div className={`side__bar offcanvas offcanvas-end ${isOpenMenu ? "show" : ""}`}>
        <div className="offcanvas-header">
          <button type="button" className="btn-close" aria-label="Close" onClick={() => setIsOpenMenu(false)}></button>
        </div>
        <div className="offcanvas-body">
          {MenuData.map((item, i) =>
            <div key={i}>
              {item.has_dropdown ?

                <>
                  {(item.title.replace("+", "").trim() !== 'More') ?
                    <div className="accordion accordion-flush shheader__accordion" id="accordionFlushExample">
                      <div className="accordion-item sh-accordion-item">
                        <h2 className="accordion-header">
                          <button className="accordion-button sh-accordion-btn collapsed" type="button" data-bs-toggle="collapse" data-bs-target={`#flush-collapse${i}`} aria-expanded="false" aria-controls={`flush-collapse${i}`}>
                            {item.title.replace("+", "")}
                          </button>
                        </h2>
                        <div id={`flush-collapse${i}`} className="accordion-collapse collapse" data-bs-parent="#accordionFlushExample">
                          <div className="accordion-body accordion-collapse-bg-gray2">
                            {item.menus?.map((menu, index) =>
                              <>
                                {menu.heading &&
                                  <div key={index} className="accordion accordion-flush" id={`accordionFlushInner${index}`}>
                                    <div className="accordion-item sh-accordion-item-inner">
                                      <h2 className="accordion-header">
                                        <button className="accordion-button collapsed " type="button" data-bs-toggle="collapse" data-bs-target={`#flush-collapse-inner${menu.heading.split(' ')[0]}${index}`} aria-expanded="false" aria-controls={`flush-collapse-inner${menu.heading.split(' ')[0]}${index}`}>
                                          {menu.heading}
                                        </button>
                                      </h2>
                                      <div id={`flush-collapse-inner${menu.heading.split(' ')[0]}${index}`} className="accordion-collapse accordion-collapse-bg-gray collapse " data-bs-parent={`#accordionFlushInner${index}`}>

                                        <div className="accordion-body">

                                          <ul className="">
                                            {menu.sub_menus?.map((submenu, index) =>
                                              <li key={index}>
                                                {item.title.replace(" +", "") === "Doctors" ?
                                                  <div className="row shheader__doctor-card" >

                                                    <div className="col-12">
                                                      <div className="shheader__doctor-icon text-center mb-10">
                                                        <img src={'/assets/img' + submenu.imgUrl} height={100} width={100} alt={submenu.title} />
                                                      </div>
                                                    </div>
                                                    <div className="col-12">
                                                      <h5 className="text-center qualification">BHMS, DRCH</h5>
                                                      <h6 className="text-center green-color designation">Chief Homoeopathy Medical Officer - State Homoeopatic Dispensary, District Hospital, Mau</h6>
                                                      <p className="bio">
                                                        Dr. {SH_DOCTOR.DR_NAMRATA} is a dedicated and compassionate BHMS-qualified doctor with 21+ years experience in homeopathic healthcare. Her journey in the field of homeopathy is marked by a commitment to providing natural and personalized healing solutions to her patients.
                                                      </p>
                                                      <div className="text-end">
                                                        <button onClick={() => knowMoreBtnClick(APP_ROUTES.DR_NAMRATA)} className="primary_btn submenu-dr-btn  mb-10">Read More</button>
                                                      </div>
                                                    </div>
                                                  </div> :
                                                  <Link to={submenu.link}>{submenu.title}</Link>
                                                }
                                              </li>)}
                                          </ul>
                                        </div>
                                      </div>
                                    </div>
                                  </div>
                                }
                              </>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                    :
                    <div className="accordion accordion-flush shheader__accordion" id="accordionFlushExample">
                      <div className="accordion-item sh-accordion-item">
                        <h2 className="accordion-header">
                          <button className="accordion-button sh-accordion-btn collapsed" type="button" data-bs-toggle="collapse" data-bs-target={`#flush-collapse${i}`} aria-expanded="false" aria-controls={`flush-collapse${i}`}>
                            {item.title.replace("+", "")}
                          </button>
                        </h2>
                        <div id={`flush-collapse${i}`} className="accordion-collapse collapse" data-bs-parent="#accordionFlushExample">
                          <div className="accordion-body accordion-collapse-bg-gray2 sh-accordion-item-inner">
                            {item.menus?.map((menu, index) =>
                              <>
                                {menu.heading &&

                                  <div className="accordion-collapse-bg-gray">
                                    <ul className="">
                                      {menu.sub_menus?.map((submenu, index) =>
                                        <li key={index}>
                                          {item.title.replace(" +", "") === "Doctors" ?
                                            <div className="row shheader__doctor-card" >

                                              <div className="col-12">
                                                <div className="shheader__doctor-icon text-center mb-10">
                                                  <img src={'/assets/img' + submenu.imgUrl} height={100} width={100} alt={submenu.title} />
                                                </div>
                                              </div>
                                              <div className="col-12">
                                                <h5 className="text-center qualification">BHMS, DRCH</h5>
                                                <h6 className="text-center green-color designation">Chief Homoeopathy Medical Officer - State Homoeopatic Dispensary, District Hospital, Mau</h6>
                                                <p className="bio">
                                                  Dr. {SH_DOCTOR.DR_NAMRATA} is a dedicated and compassionate BHMS-qualified doctor with 21+ years experience in homeopathic healthcare. Her journey in the field of homeopathy is marked by a commitment to providing natural and personalized healing solutions to her patients.
                                                </p>
                                                <div className="text-end">
                                                  <button onClick={() => knowMoreBtnClick(APP_ROUTES.DR_NAMRATA)} className="primary_btn submenu-dr-btn  mb-10">Read More</button>
                                                </div>
                                              </div>
                                            </div> :
                                            <Link to={submenu.link}>{submenu.title}</Link>
                                          }
                                        </li>)}
                                    </ul>
                                  </div>
                                }
                              </>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                  }
                </>
                :
                <li className="sidebar_sub_menu-ddnone">
                  <Link to={item.link}>
                    {item.title.replace("+", "")}
                  </Link>
                </li>
              }
            </div>
          )}
          <li className="sidebar_sub_menu-ddnone">
            <Link to="/login"><i className="fa fa-sign-in" style={{ marginRight: "16px" }} ></i>Login</Link>
          </li>
          <li className="sidebar_sub_menu-ddnone">
            <Link to="/register"><i className="fa fa-book" style={{ marginRight: "16px" }}></i>Register</Link>
          </li>
          <li className="sidebar_sub_menu-ddnone">
            <Link to="/logout"><i className="fa fa-sign-out" style={{ marginRight: "16px" }}></i>Logout</Link>
          </li>
        </div>
      </div>

    </>
  );
};

export default MobileMenus;
