
import arvind_img from '../../../assets/img/doctor/drarvind.jpg';
import namrata_img from '../../../assets/img/doctor/drnamrata.jpg';

import React from 'react';
import { APP_ROUTES } from '../../../utils/constant';
import { IDrCardDataType } from './DoctorCardData';

interface Props {
    data: IDrCardDataType;
    knowMoreBtnHandler: (url: string) => void;
}

const DoctorCard: React.FC<Props> = ({ data, knowMoreBtnHandler }) => {

    return (
        <div className="row" >
            <div className="col-3">
                {data.url === APP_ROUTES.DR_NAMRATA ?
                    <img className="img-fluid rounded rounded-circle mb-4 border border-5" src={namrata_img} alt="Dr. Namrata" />
                    :
                    <img className="img-fluid rounded rounded-circle mb-4 border border-5" src={arvind_img} alt="Dr. Arvind" />
                }
            </div>
            <div className="col-9">
                <h5 className='qualification'>{data.qualification}</h5>
                <h6 className='position'>{data.position}</h6>
                <div className='bio'>{data.bio}</div>
                <div className="text-end">
                    <button onClick={() => knowMoreBtnHandler(data.url)} className="primary_btn submenu-dr-btn  mt-20">Know More</button>
                </div>
            </div>

        </div>
    );
};

export default DoctorCard

